import React, { useEffect, useState } from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import GalleryService from "../../../../services/GalleryService";
import {useLocation} from 'react-router-dom';
import Select from "react-select";
import {AvField, AvForm} from "availity-reactstrap-validation";
import { useSelector } from "react-redux";
import { Translate } from "../../../Enums/Tranlate";
import BaseService from "../../../../services/BaseService";
import Loader from "../../../common/Loader";
import '../style.scss'

const AddGallery = () => {
   const location = useLocation();
   const [formData, setFormData] = useState({
      title: "",
      banner_image: "",
      images: []
   })
   const [loading, setLoading] = useState(false)
   const [loadingImages, setLoadingImages] = useState(false)
   const [id, setId] = useState(null)
   const [view, setView] = useState(false)
   const navigate = useNavigate()
   const galleryService = new GalleryService()
   const lang = useSelector(state=> state.auth?.lang)

   useEffect(()=>{
      if(location?.pathname.includes('edit') || location?.pathname.includes('view')){
         let item = location.state
         setId(item?.id)
         setFormData({
            title: item?.title,
            banner_image: item?.banner_image,
            images: item?.gallery_images?.map(res=> res?.url)
         })
      }
      if(location?.pathname.includes('view')){
         setView(true)
      }
   },[location])

   const inputHandler = (e) =>{
      setFormData({...formData, [e.target.name]: e.target.value})
   }

   const fileHandler = (e, type) => {
      let files = e.target.files
      const filesData = Object.values(files)

      if(!filesData?.length){
         return
      }
      if(type === "banner_image" && !!filesData?.length){
         setLoading(true)
         new BaseService().postUpload(filesData[0]).then(res=>{
             if(res?.status === 200){
                 setFormData({...formData, banner_image: res?.data?.url})
             }
             setLoading(false)
         }).catch(()=> setLoading(false))
      }
      if(type === "images" && !!filesData?.length){
         setLoadingImages(true)
         Promise.all(filesData.map(res=> new BaseService().postUpload(res))).then(res=> {
            let response = res?.filter(item => item?.status === 200)
            if(!!response?.length){
               setFormData({...formData, images: [...formData?.images, ...response.map(img=> img?.data?.url)]})
            }
            setLoadingImages(false)
         }).catch(()=> setLoadingImages(false))
      }
   }

   const onSubmit = (e) =>{
      e.preventDefault();
      
      let data = {
         title: formData?.title,
         banner_image: formData?.banner_image,
         gallery_images: formData?.images?.slice(0,10)
      }

      if(!!id){
         setLoading(true)
         galleryService.update(id, data).then((response) =>{
            if(response?.status === 200){
               toast.success(Translate[lang].admin_updated_successfully)
               navigate('/gallery')
            }
            setLoading(false)
         })
      } else {
         setLoading(true)
         galleryService.create(data).then((response) =>{
            if(response?.status === 201){
               toast.success(`${Translate[lang].added} ${Translate[lang].gallery}`)
               navigate('/gallery')
            }
            setLoading(false)
         })
      }
   }

   return (<>
      <div className="d-flex  align-items-center" style={{gap: '8px', margin: '12px 0 32px'}}>
         <svg style={{cursor: 'pointer'}} width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg" onClick={()=> navigate('/halls')}>
            <path d="M9.57 5.92993L3.5 11.9999L9.57 18.0699" stroke="#1E1F24" stroke-width="1.5" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"/>
            <path d="M20.4999 12H3.66992" stroke="#1E1F24" stroke-width="1.5" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"/>
         </svg>

         <label className="mb-0" style={{fontSize: '20px', fontWeight: '600'}}>
            {view ? Translate[lang]?.view : !!id ? Translate[lang]?.edit : Translate[lang]?.add} {Translate[lang]?.gallery}
         </label>
      </div>
      <Row className="add-gallery">
         <Col md={10}>
            <AvForm onValidSubmit={onSubmit}>
               <div className="row">
                  <div className="col-lg-6 col-sm-6 ">
                     <AvField
                        label ={`${Translate[lang]?.title}`}
                        name ='title'
                        type='text'
                        value={formData?.title}
                        disabled={view}
                        validate={{
                           required: {
                              value:true,
                              errorMessage: Translate[lang].field_required
                           },
                        }}
                        placeholder={`${Translate[lang]?.title}`}
                        onChange={(e)=> inputHandler(e)}
                     />
                  </div>
                  <div className="col-lg-12 col-sm-12 mb-3">
                     <div className='form-group w-100'>
                        <label className="m-0">{Translate[lang]?.banner_image}</label>
                        <div className="image-placeholder">	
                           {!view && <div className="avatar-edit">
                             {!loading && <input accept="image/*" type="file" onChange={(e) => fileHandler(e, "banner_image")} id={`imageUpload1`} />}
                              <label htmlFor={`imageUpload1`}  name=''></label>
                           </div>}
                           <div className="avatar-preview2 m-auto">
                              <div id={`imagePreview`} className="images">
                                    <>
                                       <i className="la la-plus-circle" />
                                       <span style={{fontSize: '18px', fontWeight: '600'}}>{Translate[lang]?.add} {Translate[lang]?.image}</span>
                                       <span style={{fontSize: '12px'}}>{Translate[lang]?.max_size} 20 {Translate[lang]?.mbs}</span>
                                    </>
                                 {loading && <Loader />}
                              </div>
                           </div>
                        </div>
                        {!!formData?.banner_image && <div className="banner_image">
                           <img src={formData?.banner_image} alt="banner_image" />
                           <a href={formData?.banner_image} target="_blank" rel="noreferrer">
                              <i className="la la-eye" />
                           </a>
                           {!view && <i className="la la-trash" onClick={()=> setFormData({...formData, banner_image: ""})} />}
                        </div>}
                     </div>
                  </div>
                  <div className="col-lg-12 col-sm-12 mb-3">
                     <div className='form-group w-100'>
                        <label className="m-0">{Translate[lang]?.images}</label>
                        <div className="image-placeholder">	
                           {!view && <div className="avatar-edit">
                              {(!loadingImages || formData?.images?.length <= 10) && <input 
                                 accept="image/*" 
                                 type="file" 
                                 multiple 
                                 onChange={(e) => fileHandler(e, "images")} id={`imageUpload2`} /> }					
                              <label htmlFor={`imageUpload2`}  name=''></label>
                           </div>}
                           <div className="avatar-preview2 m-auto">
                              <div id={`imagePreview`} className="images">
                                 <>
                                       <i className="la la-plus-circle" />
                                       <span style={{fontSize: '18px', fontWeight: '600'}}>{Translate[lang]?.add} {Translate[lang]?.image}</span>
                                       <span style={{fontSize: '12px'}}>{Translate[lang]?.max_number} 10 {Translate[lang]?.images}</span>
                                       <span style={{fontSize: '12px'}}>{Translate[lang]?.max_size} 20 {Translate[lang]?.mbs}</span>
                                    </>
                                 {loadingImages && <Loader />}
                              </div>
                           </div>
                        </div>
                        {!!formData?.images?.length && <div className="row">
                           {formData?.images?.slice(0,10)?.map((res, index)=> {
                              return <div className="col-md-3">
                                 <div className="banner_image w-auto">
                                    <img src={res} alt="banner_image" className="w-100" />
                                    <a href={res} target="_blank" rel="noreferrer">
                                       <i className="la la-eye" />
                                    </a>
                                    {!view && <i className="la la-trash" 
                                       onClick={()=> {
                                          let update = formData?.images?.filter((_,ind) => ind !== index)
                                          setFormData({...formData, images: update})
                                       }} 
                                    />}
                              </div>
                              </div>
                           })}
                        </div>}
                     </div>
                  </div>
                  <div className="col-lg-6 col-sm-6 mt-4 mb-5">
                     {view && <Button variant="secondary" type="button" className='w-100 fs-14' onClick={()=> navigate('/gallery')}>{Translate[lang]?.cancel}</Button>}
                     {!view && <Button variant="primary" type="submit" className='w-100 fs-14' disabled={loading}>{Translate[lang]?.save}</Button>}
                  </div>
               </div>
            </AvForm>
         </Col>
      </Row>
   </>
   );
};

export default AddGallery;
