import React, { useEffect, useState } from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import {useLocation} from 'react-router-dom';
import {AvField, AvForm} from "availity-reactstrap-validation";
import { useSelector } from "react-redux";
import { Translate } from "../../Enums/Tranlate";
import BaseService from "../../../services/BaseService";
import Loader from "../../common/Loader";
import './style.scss';
import StaticService from "../../../services/StaticService";

const About = () => {
   const location = useLocation();
   const [formData, setFormData] = useState({
      title: "",
      description: "",
      image: "",
      total_active_students: "",
      available_programs_and_camps: "",
      available_educational_courses: "",
   })
   const [loading, setLoading] = useState(false)
   const [id, setId] = useState(null)
   const [view, setView] = useState(false)
   const navigate = useNavigate()
   const lang = useSelector(state=> state.auth?.lang)
   const staticService = new StaticService()

   useEffect(()=>{
      staticService.getList({type: 'about'}).then(res=> {
         if(res?.status){
            let response = res?.data?.data
            if(!!response?.length){
               setFormData({
                  title: response[0]?.title,
                  description: response[0]?.description,
                  image: response[0]?.image,
                  total_active_students: response[0]?.total_active_students,
                  available_programs_and_camps: response[0]?.available_programs_and_camps,
                  available_educational_courses: response[0]?.available_educational_courses,
               })
            }
         }
      })
   },[])

   const inputHandler = (e) =>{
      setFormData({...formData, [e.target.name]: e.target.value})
   }

   const onSubmit = (e) =>{
      e.preventDefault();
      
      if(!formData?.image || !formData?.title || !formData?.description){
         if(!formData?.image) toast.error("Image Required")
         if(!formData?.title) toast.error("Title Required")
         if(!formData?.description) toast.error("Description Required")
         return 
      }
      let info = {
         title: formData?.title,
         description: formData?.description,
         image: formData?.image,
      }
      if(!!formData?.available_educational_courses) info["available_educational_courses"] = formData?.available_educational_courses
      if(!!formData?.available_programs_and_camps) info["available_programs_and_camps"] = formData?.available_programs_and_camps
      if(!!formData?.total_active_students) info["total_active_students"] = formData?.total_active_students
      let data = {
         static_page: [info],
         type: "about"
       }

      setLoading(true)
      staticService.create(data).then((response) =>{
         if(response?.status === 201){
            toast.success(`${Translate[lang].added} ${Translate[lang].admin} ${Translate[lang].successfully}`)
         }
         setLoading(false)
      })
   }

   const fileHandler = (e) => {
      let files = e.target.files
      const filesData = Object.values(files)
      setLoading(true)
      if (filesData?.length) {
          new BaseService().postUpload(filesData[0]).then(res=>{
              if(res?.status === 200){
                  setFormData({...formData, image: res?.data?.url})
              }
              setLoading(false)
          }).catch(()=> setLoading(false))
      }
   }

   return (<>
      <Row className="about">
         <Col md={10}>
            <AvForm onValidSubmit={onSubmit}>
               <div className="row">
                  <div className="col-lg-12 col-sm-12 mb-3">
                     <label>{Translate[lang].title}</label>
                     <textarea
                        name="title"
                        value={formData.title}
                        placeholder={Translate[lang].title}
                        onChange={e=> setFormData({...formData, title: e.target?.value})}
                        className="w-100"
                     />
                     <span className="max">{Translate[lang].Max} 200 {Translate[lang].Word}</span>
                  </div>
                  <div className="col-lg-12 col-sm-12 mb-3">
                     <label>{Translate[lang].description}</label>
                     <textarea
                        name="description"
                        value={formData.description}
                        placeholder={Translate[lang].description}
                        onChange={e=> setFormData({...formData, description: e.target?.value})}
                        className="w-100"
                     />
                     <span className="max">{Translate[lang].Max} 2000 {Translate[lang].Word}</span>
                  </div>
                  <div className="col-lg-12 col-sm-12 mb-3">
                     <div className='form-group w-100'>
                        <label className="m-0">{Translate[lang]?.image}</label>
                        <div className="image-placeholder">	
                           <div className="avatar-edit">
                              <input type="file" onChange={(e) => fileHandler(e)} id={`imageUpload1`} /> 					
                              <label htmlFor={`imageUpload1`}  name=''></label>
                           </div>
                           <div className="avatar-preview2 m-auto">
                              <div id={`imagePreview`} className="images">
                                 <>
                                       <i className="la la-plus-circle" />
                                       <span style={{fontSize: '18px', fontWeight: '600'}}>{Translate[lang]?.add} {Translate[lang]?.image}</span>
                                       <span style={{fontSize: '12px'}}>{Translate[lang]?.max_size} 20 {Translate[lang]?.mbs}</span>
                                    </>
                                    {loading && <Loader />}
                              </div>
                           </div>
                        </div>
                        {!!formData?.image && <div className="about_image">
                           <img src={formData?.image} alt="banner_image" />
                           <a href={formData?.image} target="_blank" rel="noreferrer">
                              <i className="la la-eye" />
                           </a>
                           {!view && <i className="la la-trash" onClick={()=> setFormData({...formData, image: ""})} />}
                        </div>}
                     </div>
                  </div>
                  <div className="col-lg-6 col-sm-6 ">
                     <AvField
                        label ={`${Translate[lang]?.Total_active_students}`}
                        name ='total_active_students'
                        type='number'
                        value={formData?.total_active_students}
                        // validate={{
                        //    required: {
                        //       value:true,
                        //       errorMessage: Translate[lang].field_required
                        //    },
                        // }}
                        placeholder={`${Translate[lang]?.number}`}
                        onChange={(e)=> inputHandler(e)}
                     />
                  </div>
                  <div className="col-lg-6 col-sm-6 ">
                     <AvField
                        label ={`${Translate[lang]?.Available_programs_and_camps}`}
                        name ='available_programs_and_camps'
                        type='number'
                        value={formData?.available_programs_and_camps}
                        // validate={{
                        //    required: {
                        //       value:true,
                        //       errorMessage: Translate[lang].field_required
                        //    },
                        // }}
                        placeholder={`${Translate[lang]?.number}`}
                        onChange={(e)=> inputHandler(e)}
                     />
                  </div>
                  <div className="col-lg-6 col-sm-6 ">
                     <AvField
                        label ={`${Translate[lang]?.Available_Educational_Courses}`}
                        name ='available_educational_courses'
                        type='number'
                        value={formData?.available_educational_courses}
                        // validate={{
                        //    required: {
                        //       value:true,
                        //       errorMessage: Translate[lang].field_required
                        //    },
                        // }}
                        placeholder={`${Translate[lang]?.number}`}
                        onChange={(e)=> inputHandler(e)}
                     />
                  </div>
                  <div className="col-lg-6 col-sm-6"></div>
                  <div className="col-lg-6 col-sm-6 mt-4 mb-5">
                     {!view && <Button variant="primary" type="submit" className='w-100 fs-14' disabled={loading}>{Translate[lang]?.save}</Button>}
                  </div>
               </div>
            </AvForm>
         </Col>
      </Row>
   </>
   );
};

export default About;
