import { useEffect, useState } from "react";
import { Button, Col, Form, Modal, Row } from "react-bootstrap"
import {AvField, AvForm} from "availity-reactstrap-validation";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import { Translate } from "../../../Enums/Tranlate";
import Select from "react-select";
import '../style.scss'

const AddDepartmentsModal = ({addModal, setAddModal, item, setShouldUpdate, type, Service})=>{
    const [formData, setFormData] = useState({
        department_name: '',
        organization: '',
        department_manager: '',
        unit_name: '',
        department: '',
        unit_manager: '',
    })
    const [isAdd, setIsAdd] = useState(false)
    const [loading, setLoading] = useState(false)
    const lang = useSelector(state=> state.auth.lang)

    useEffect(() => {
        if(Object.keys(item)?.length === 0){
            setIsAdd(true)
        } else {
            setIsAdd(false)
            setFormData({
                id: item?.id,
                name: item?.name
            })
        }
    },[item])

    const submit = (e) =>{
        e.preventDefault();
        let data = {}
        if(type === 'departments'){

        }
        setLoading(true)
        if(isAdd){
            Service.create(data)?.then(res=>{
                if(res && res?.status === 201){
                    toast.success(`${type === 'departments' ? Translate[lang]?.department : Translate[lang]?.unit} Added Successfully`)
                    setShouldUpdate(prev=> !prev)
                    setAddModal()
                }
                setLoading(false)
            }).catch(()=>setLoading(false))
        } else {
            Service.update(formData?.id, data)?.then(res=>{
                if(res && res?.status === 200){
                    toast.success(`${type === 'departments' ? Translate[lang]?.department : Translate[lang]?.unit} Updated Successfully`)
                    setShouldUpdate(prev=> !prev)
                    setAddModal()
                }
                setLoading(false)
            }).catch(()=>setLoading(false))
        }
    }

    return(
        <Modal className={lang === 'en' ? "en fade addDepartment" : "ar fade addDepartment"} style={{textAlign: lang === 'en' ? 'left' : 'right'}} show={addModal} onHide={()=>{
            setAddModal()
            }}>
                <AvForm
                    className='form-horizontal'
                    onValidSubmit={submit}>
            <Modal.Body>
                    <Row>
                        <Col md={12} className='mb-2'>
                            <div className="d-flex justify-content-between">
                                <p className="mb-0"></p>
                                <Button
                                    variant=""
                                    className="close"
                                    style={{right: lang === 'en' ? '0' : 'auto', left: lang === 'ar' ? '0' : 'auto'}}
                                    onClick={()=>{
                                        setAddModal()
                                    }}
                                    >
                                    <span style={{fontSize: '22px'}}>&times;</span>
                                </Button>
                            </div>
                        </Col>
                        <Col md={12} className='mb-4'>
                            <div className="d-flex justify-content-between align-items-center">
                                <p className="mb-0" style={{fontWeight: '600', color: '#333', fontSize: '20px'}}>{isAdd ? Translate[lang]?.add : Translate[lang]?.edit} {Translate[lang]?.department}</p>
                                <Form.Check
                                    type="switch"
                                    id="custom-switch"
                                    checked={formData?.status}
                                    onChange={(e) => setFormData({...formData, status: e.target.checked})}
                                />
                            </div>
                        </Col>
                        {type === "departments" && <Col md={12}>
                            <AvField
                                label={Translate[lang]?.department_name}
                                type='text'
                                placeholder={Translate[lang]?.department_name}
                                bsSize="lg"
                                name='department_name'
                                validate={{
                                    required: {
                                        value: true,
                                        errorMessage: Translate[lang].field_required
                                    }
                                }}
                                value={formData.department_name}
                                onChange={(e) => setFormData({...formData, department_name: e.target.value})}
                            />
                        </Col>}
                        {type === "departments" && <Col md={12} className='mb-3'>
                            <label className="text-label">{Translate[lang]?.organization}</label>
                            <Select
                                value={formData?.organization}
                                name="organization"
                                placeholder={Translate[lang]?.select}
                                options={[]}
                                onChange={(e)=> setFormData({...formData, organization: e})}
                            />
                        </Col>}
                        {type === "departments" && <Col md={12}>
                            <label className="text-label">{Translate[lang]?.department_manager}</label>
                            <Select
                                value={formData?.department_manager}
                                name="department_manager"
                                placeholder={Translate[lang]?.select}
                                options={[]}
                                onChange={(e)=> setFormData({...formData, department_manager: e})}
                            />
                        </Col>}

                        {type === "units" && <Col md={12}>
                            <AvField
                                label={Translate[lang]?.unit_name}
                                type='text'
                                placeholder={Translate[lang]?.unit_name}
                                bsSize="lg"
                                name='unit_name'
                                validate={{
                                    required: {
                                        value: true,
                                        errorMessage: Translate[lang].field_required
                                    }
                                }}
                                value={formData.unit_name}
                                onChange={(e) => setFormData({...formData, unit_name: e.target.value})}
                            />
                        </Col>}
                        {type === "units" && <Col md={12} className='mb-3'>
                            <label className="text-label">{Translate[lang]?.department}</label>
                            <Select
                                value={formData?.department}
                                name="department"
                                placeholder={Translate[lang]?.select}
                                options={[]}
                                onChange={(e)=> setFormData({...formData, department: e})}
                            />
                        </Col>}
                        {type === "units" && <Col md={12}>
                            <label className="text-label">{Translate[lang]?.unit_manager}</label>
                            <Select
                                value={formData?.unit_manager}
                                name="unit_manager"
                                placeholder={Translate[lang]?.select}
                                options={[]}
                                onChange={(e)=> setFormData({...formData, unit_manager: e})}
                            />
                        </Col>}
                    </Row>
            </Modal.Body>
            <Modal.Footer>
            {/* <Button onClick={setAddModal} variant="danger light">
            {Translate[lang]?.close}
            </Button> */}
            <Button 
                    variant="primary" 
                    type='submit'
                    className='w-100'
                    disabled={loading}
                >{isAdd ? Translate[lang]?.add : Translate[lang]?.edit} {type === 'departments' ? Translate[lang]?.department : Translate[lang]?.unit}</Button>
            </Modal.Footer>
            </AvForm>
        </Modal>)
}

export default AddDepartmentsModal;