import React, { useEffect, useState } from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import {useLocation} from 'react-router-dom';
import Select from "react-select";
import {AvField, AvForm} from "availity-reactstrap-validation";
import { useSelector } from "react-redux";
import { Translate } from "../../../Enums/Tranlate";
import Loader from "../../../common/Loader";
import BaseService from "../../../../services/BaseService";
import BlogsService from "../../../../services/BlogsService";

const AddBlog = () => {
   const location = useLocation();
   const [formData, setFormData] = useState({
      title: "",
      description: "",
      image: "",
      time: "",
      category: "",
      date: "",
      status: ""
   })
   const [loading, setLoading] = useState(false)
   const [id, setId] = useState(null)
   const [view, setView] = useState(false)
   const navigate = useNavigate()
   const lang = useSelector(state=> state.auth?.lang)
   const statusOptions = [
      {label: Translate[lang].published, value: "published"},
      {label: Translate[lang].drafted, value: "drafted"},
   ]

   useEffect(()=>{
      if(location?.pathname?.includes('edit') || location?.pathname?.includes('view')){
         let item = location.state
         setId(item?.id)
         setFormData({
            title: item?.title,
            description: item?.description,
            image: item?.image,
            time: item?.read_time,
            category: item?.category,
            date: item?.published_date,
            status: statusOptions?.find(info => info?.value === item?.status)
         })
      }
      if(location?.pathname?.includes('view')){
         setView(true)
      }
   },[location])

   const inputHandler = (e) =>{
      setFormData({...formData, [e.target.name]: e.target.value})
   }

   const onSubmit = (e) =>{
      e.preventDefault();
      if(!formData?.image || !formData?.title || !formData?.description || !formData?.status){
         if(!formData?.image) toast.error("Image Required")
         if(!formData?.title) toast.error("Title Required")
         if(!formData?.description) toast.error("Description Required")
         if(!formData?.status) toast.error("Status Required")
         return 
      }

      let data = {
         title: formData?.title,
         description: formData?.description,
         image: formData?.image,
         read_time: formData?.time,
         published_date: formData?.date,
         category: formData?.category,
         status: formData?.status?.value
      }

      if(!!id){
         setLoading(true)
         new BlogsService().update(id, data).then((response) =>{
            if(response?.status === 200){
               toast.success(Translate[lang].updated_successfully)
               navigate('/blogs')
            }
            setLoading(false)
         })
      } else {
         setLoading(true)
         new BlogsService().create(data).then((response) =>{
            if(response?.status === 201){
               toast.success(`${Translate[lang].added} ${Translate[lang].successfully}`)
               navigate('/blogs')
            }
            setLoading(false)
         })
      }
   }

   const fileHandler = (e) => {
      let files = e.target.files
      const filesData = Object.values(files)
      if(!filesData?.length){
         return
      }
      setLoading(true)
      if (filesData?.length) {
          new BaseService().postUpload(filesData[0]).then(res=>{
              if(res?.status === 200){
                  setFormData({...formData, image: res?.data?.url})
              }
              setLoading(false)
          }).catch(()=> setLoading(false))
      }
   }

   return (<>
      <div className="d-flex  align-items-center" style={{gap: '8px', margin: '12px 0 32px'}}>
         <svg style={{cursor: 'pointer'}} width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg" onClick={()=> navigate('/blogs')}>
            <path d="M9.57 5.92993L3.5 11.9999L9.57 18.0699" stroke="#1E1F24" stroke-width="1.5" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"/>
            <path d="M20.4999 12H3.66992" stroke="#1E1F24" stroke-width="1.5" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"/>
         </svg>

         <label className="mb-0" style={{fontSize: '20px', fontWeight: '600'}}>
            {view ? Translate[lang]?.view : !!id ? Translate[lang]?.edit : Translate[lang]?.add} {Translate[lang]?.blog}
         </label>
      </div>
      <Row className="about">
         <Col md={10}>
            <AvForm onValidSubmit={onSubmit}>
               <div className="row">
                  <div className="col-lg-12 col-sm-12 mb-3">
                     <label>{Translate[lang].title}</label>
                     <textarea
                        name="title"
                        value={formData.title}
                        placeholder={Translate[lang].title}
                        onChange={e=> setFormData({...formData, title: e.target?.value})}
                        className="w-100"
                        required
                        disabled={view}
                     />
                     <span className="max">{Translate[lang].Max} 200 {Translate[lang].Word}</span>
                  </div>
                  <div className="col-lg-12 col-sm-12 mb-3">
                     <label>{Translate[lang].description}</label>
                     <textarea
                        name="description"
                        value={formData.description}
                        disabled={view}
                        required
                        placeholder={Translate[lang].description}
                        onChange={e=> setFormData({...formData, description: e.target?.value})}
                        className="w-100"
                     />
                     <span className="max">{Translate[lang].Max} 2000 {Translate[lang].Word}</span>
                  </div>
                  <div className="col-lg-12 col-sm-12 mb-3">
                     <div className='form-group w-100'>
                        <label className="m-0">{Translate[lang]?.image}</label>
                        <div className="image-placeholder">	
                           <div className="avatar-edit">
                              {!loading && <input type="file" onChange={(e) => fileHandler(e)} id={`imageUpload1`} /> }					
                              <label htmlFor={`imageUpload1`}  name=''></label>
                           </div>
                           <div className="avatar-preview2 m-auto">
                              <div id={`imagePreview`} className="images">
                                 <>
                                       <i className="la la-plus-circle" />
                                       <span style={{fontSize: '18px', fontWeight: '600'}}>{Translate[lang]?.add} {Translate[lang]?.image}</span>
                                       <span style={{fontSize: '12px'}}>{Translate[lang]?.max_size} 20 {Translate[lang]?.mbs}</span>
                                    </>
                                    {loading && <Loader />}
                              </div>
                           </div>
                        </div>
                        {!!formData?.image && <div className="about_image">
                           <img src={formData?.image} alt="banner_image" />
                           <a href={formData?.image} target="_blank" rel="noreferrer">
                              <i className="la la-eye" />
                           </a>
                           {!view && <i className="la la-trash" onClick={()=> setFormData({...formData, image: ""})} />}
                        </div>}
                     </div>
                  </div>
                  <div className="col-lg-6 col-sm-6 ">
                     <AvField
                        label ={Translate[lang]?.time}
                        name ='time'
                        type='text'
                        value={formData?.time}
                        validate={{
                           required: {
                              value:true,
                              errorMessage: Translate[lang].field_required
                           },
                        }}
                        disabled={view}
                        placeholder={Translate[lang]?.time}
                        onChange={(e)=> inputHandler(e)}
                     />
                  </div>
                  <div className="col-lg-6 col-sm-6 ">
                     <AvField
                        label ={Translate[lang]?.date}
                        name ='date'
                        type='date'
                        value={formData?.date}
                        disabled={view}
                        validate={{
                           required: {
                              value:true,
                              errorMessage: Translate[lang].field_required
                           },
                        }}
                        onChange={(e)=> inputHandler(e)}
                     />
                  </div>
                  <div className="col-lg-6 col-sm-6 ">
                     <AvField
                        label ={Translate[lang]?.category}
                        name ='category'
                        type='text'
                        disabled={view}
                        value={formData?.category}
                        validate={{
                           required: {
                              value:true,
                              errorMessage: Translate[lang].field_required
                           },
                        }}
                        placeholder={Translate[lang]?.category}
                        onChange={(e)=> inputHandler(e)}
                     />
                  </div>
                  <div className="col-lg-6 col-sm-6 ">
                     <label className="text-label">
                        {Translate[lang]?.status}
                     </label>
                     <Select
                        value={formData?.status}
                        name="status"
                        disabled={view}
                        placeholder={Translate[lang]?.status}
                        options={statusOptions}
                        onChange={(e)=> setFormData({...formData, status: e})}
                     />
                  </div>
                  
                  <div className="col-lg-6 col-sm-6 mt-4 mb-5">
                     {view && <Button variant="secondary" type="button" className='w-100 fs-14' onClick={()=> navigate('/blogs')}>{Translate[lang]?.cancel}</Button>}
                     {!view && <Button variant="primary" type="submit" className='w-100 fs-14' disabled={loading}>{Translate[lang]?.save}</Button>}
                  </div>
               </div>
            </AvForm>
         </Col>
      </Row>
   </>
   );
};

export default AddBlog;
