import { useState } from "react";
import { Badge, Dropdown } from "react-bootstrap";
import { useSelector } from "react-redux";
import DepartmentService from "../../../../services/DepartmentService";
import DeleteModal from "../../../common/DeleteModal";
import { Translate } from "../../../Enums/Tranlate";
import editIcon from "../../../../images/icons/edit.svg";
import trashIcon from "../../../../images/icons/delete.svg";

const CardItem = ({item, setItem, index, setAddModal,setViewModal, setShouldUpdate}) =>{
    const [deleteModal, setDeleteModal] = useState(false)
    const Auth = useSelector(state=> state.auth?.auth)
    const lang = useSelector(state=> state.auth?.lang)
    const isExist = (data)=> Auth?.admin?.admin_roles?.includes(data)
    const departmentService = new DepartmentService()

    return(
        <tr key={index} className='text-center'>
            <td>
                {item?.title}
            </td>
            <td>{item?.description}</td>
            <td className="d-flex align-items-center text-capitalize" style={{gap: '8px'}}>
                <span
                    style={{
                        background: getBackground(item?.color),
                        height: '32px',
                        width: '32px',
                        borderRadius: '50%',
                        display: 'inline-block'
                    }}
                ></span>
                {item?.color}
            </td>
            <td>
                <Badge
                    className="text-capitalize"
                    variant={`${
                        item.status === "active"
                        ? "success"
                        // : item.status === "canceled"
                        // ? "danger"
                        // : item.status === "ordered"
                        // ? "primary"
                        // : item.status === "pending"
                        // ? "warning"
                        // : item.status === "success"
                        // ? "info"
                        : "success"
                    } light`}
                >
                    {item?.status || 'Active'}
                </Badge>
            </td>
            <td>
                <div className="d-flex" style={{gap: '20px'}}>
                    <img src={editIcon} alt='edit' width={24} className="cursor-pointer" onClick={()=> setAddModal(true)} />
                    <img src={trashIcon} alt='trash' width={24} className="cursor-pointer" />
                </div>
            </td>
            {deleteModal && <DeleteModal
                      open={deleteModal}
                      titleMsg={item?.name}
                      deletedItem={item}
                      modelService={departmentService}
                      onCloseModal={setDeleteModal}
                      setShouldUpdate={setShouldUpdate}
                    />}
            </tr>
    )
}
const getBackground = (type) => {
    if(type === 'orange'){
        return '#FFCABA'
    } else if(type === 'yellow'){
        return '#FFDEAD'
    } else if(type === 'blue'){
        return '#ECF3FC'
    } else if(type === 'red'){
        return '#FFE5EB'
    } else if(type === 'green'){
        return '#EFFBF2'
    } else if(type === 'gray'){
        return '#F7F7F7'
    } else {
        return '#A1FFF3'
    }
}
export default CardItem;