import React, { useEffect, useState } from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import {useLocation} from 'react-router-dom';
import Select from "react-select";
import {AvField, AvForm} from "availity-reactstrap-validation";
import { useSelector } from "react-redux";
import { Translate } from "../../Enums/Tranlate";
import BaseService from "../../../services/BaseService";
import uploadImg from "../../../images/upload-img.png";
import Loader from "../../common/Loader";
import './style.scss';
import BannerService from "../../../services/BannerService";

const Banner = () => {
   const [formData, setFormData] = useState("")
   const [loading, setLoading] = useState(false)
   const [deleteLoading, setDeleteLoading] = useState(false)
   const [shouldUpdate, setShouldUpdate] = useState(false)
   const [id, setId] = useState(null)
   const lang = useSelector(state=> state.auth?.lang)
   const bannerService = new BannerService()

   useEffect(()=>{
      bannerService.getList().then(res=>{
         if(res?.status){
            if(!!res?.data?.data?.length){ 
               setFormData(res?.data?.data[0]?.image)
               setId(res?.data?.data[0]?.id)
            }
         }
      })
   },[shouldUpdate])

   const onSubmit = (img) =>{
      let data = {
         banners: [
           {
             image: img
           }
         ]
       }

      if(!!id){
         setLoading(true)
         bannerService.update(data).then((response) =>{
            if(response?.status === 200){
               toast.success(Translate[lang].updated_successfully)
               setShouldUpdate(prev => !prev)
            }
            setLoading(false)
         })
      } else {
         setLoading(true)
         bannerService.create(data).then((response) =>{
            if(response?.status === 201){
               toast.success(`${Translate[lang].added} ${Translate[lang].successfully}`)
               setShouldUpdate(prev => !prev)
            }
            setLoading(false)
         })
      }
   }

   const deleteBanner = () => {
      setDeleteLoading(true)
      bannerService.remove(id).then((response) =>{
         if(response?.status){
            toast.success(`${Translate[lang].removed} ${Translate[lang].successfully}`)
            setId("")
            setFormData("")
            setShouldUpdate(prev => !prev)
         }
         setDeleteLoading(false)
      })
   }
   const fileHandler = (e) => {
      let files = e.target.files
      const filesData = Object.values(files)
      setLoading(true)
      if (filesData?.length) {
          new BaseService().postUpload(filesData[0]).then(res=>{
              if(res?.status === 200){
                  setFormData(res?.data?.url)
                  onSubmit(res?.data?.url)
              }
              setLoading(false)
          }).catch(()=> setLoading(false))
      }
   }

   return (<>
      <div style={{margin: '12px 0 32px'}}>
         <label className="mb-0" style={{fontSize: '20px', fontWeight: '600'}}>
            {Translate[lang]?.banner}
         </label>
      </div>
      <Row>
         <Col md={10}>
            <div>
               <div className="row">
                  <div className="col-lg-12 col-sm-12 mb-3">
                     <div className='form-group w-100'>
                        <div className="banner">
                           <div className="banner-img">
                              {!formData && <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                 <path d="M9 22H15C20 22 22 20 22 15V9C22 4 20 2 15 2H9C4 2 2 4 2 9V15C2 20 4 22 9 22Z" stroke="#004094" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                                 <path d="M9 10C10.1046 10 11 9.10457 11 8C11 6.89543 10.1046 6 9 6C7.89543 6 7 6.89543 7 8C7 9.10457 7.89543 10 9 10Z" stroke="#004094" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                                 <path d="M2.66992 18.9486L7.59992 15.6386C8.38992 15.1086 9.52992 15.1686 10.2399 15.7786L10.5699 16.0686C11.3499 16.7386 12.6099 16.7386 13.3899 16.0686L17.5499 12.4986C18.3299 11.8286 19.5899 11.8286 20.3699 12.4986L21.9999 13.8986" stroke="#004094" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                              </svg>}
                              {!!formData && <a href={formData} target="_blank" rel="noreferrer" className='w-100 h-100'>
                                 <img src={formData} alt="banner" className='w-100 h-100' />
                              </a>}
                           </div>
                           <div className="text-center">
                              <span className="max">{Translate[lang]?.max_size} 100 {Translate[lang]?.mbs}</span>
                              <div className="d-flex mt-4" style={{gap: '24px'}}>
                                 <label class="custum-file-upload" for="file">
                                    <div class="icon">
                                       <svg width="25" height="25" viewBox="0 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg">
                                          <path d="M16.9405 9.39844C20.5405 9.70844 22.0105 11.5584 22.0105 15.6084V15.7384C22.0105 20.2084 20.2205 21.9984 15.7505 21.9984H9.24047C4.77047 21.9984 2.98047 20.2084 2.98047 15.7384V15.6084C2.98047 11.5884 4.43047 9.73844 7.97047 9.40844" stroke="#FDFDFE" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                                          <path d="M12.5 15.5011V4.12109" stroke="#FDFDFE" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                                          <path d="M15.8504 6.35L12.5004 3L9.15039 6.35" stroke="#FDFDFE" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                                       </svg>
                                    </div>
                                    <div>
                                       <span class="text">{Translate[lang].upload_image}</span>
                                    </div>
                                    {!loading && <input type="file" accept="image/*" onChange={(e) => fileHandler(e)} className="upload" name="Upload" />} 
                                 </label>

                                 {!!id && <button disabled={deleteLoading} className="delete-img" onClick={deleteBanner}>
                                    <i className="la la-trash" />
                                    {Translate[lang].delete}
                                 </button>}
                              </div>
                           </div>
                        </div>
                     </div>
                  </div>
               </div>
            </div>
         </Col>
      </Row>
   </>
   );
};

export default Banner;
