import { useEffect, useState } from "react";
import { Button, Col, Form, Modal, Row } from "react-bootstrap"
import {AvField, AvForm} from "availity-reactstrap-validation";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import { Translate } from "../../../Enums/Tranlate";
import Select from "react-select";
import PhoneInput from "react-phone-input-2";

const AddBranchModal = ({addModal, setAddModal, item, setShouldUpdate})=>{
    const [formData, setFormData] = useState({
        status: true,
        branch_name: '',
        branch_manager: '',
        branch_id: '',
        branch_no: '',
        organization: '',
        email: '',
        phone: '',
        address: '',
        work_shift: '',
        gates_number: '',
        gates: [],
    })
    const [isAdd, setIsAdd] = useState(false)
    const [loading, setLoading] = useState(false)
    const lang = useSelector(state=> state.auth.lang)

    useEffect(() => {
        if(Object.keys(item)?.length === 0){
            setIsAdd(true)
        } else {
            setIsAdd(false)
            setFormData({
                id: item?.id,
                name: item?.name
            })
        }
    },[item])

    const submit = (e) =>{
        e.preventDefault();
        let data = {}

        setLoading(true)
        if(isAdd){
            // Service.create(data)?.then(res=>{
            //     if(res && res?.status === 201){
            //         toast.success(`${type === 'departments' ? Translate[lang]?.department : Translate[lang]?.unit} Added Successfully`)
            //         setShouldUpdate(prev=> !prev)
            //         setAddModal()
            //     }
            //     setLoading(false)
            // }).catch(()=>setLoading(false))
        } else {
            // Service.update(formData?.id, data)?.then(res=>{
            //     if(res && res?.status === 200){
            //         toast.success(`${type === 'departments' ? Translate[lang]?.department : Translate[lang]?.unit} Updated Successfully`)
            //         setShouldUpdate(prev=> !prev)
            //         setAddModal()
            //     }
            //     setLoading(false)
            // }).catch(()=>setLoading(false))
        }
    }

    return(
        <Modal className={lang === 'en' ? "en fade addDepartment" : "ar fade addDepartment"} style={{textAlign: lang === 'en' ? 'left' : 'right'}} show={addModal} onHide={()=>{
            setAddModal()
            }}>
                <AvForm
                    className='form-horizontal'
                    onValidSubmit={submit}>
            <Modal.Body>
                    <Row>
                        <Col md={12} className='mb-2'>
                            <div className="d-flex justify-content-between">
                                <p className="mb-0"></p>
                                <Button
                                    variant=""
                                    className="close"
                                    style={{right: lang === 'en' ? '0' : 'auto', left: lang === 'ar' ? '0' : 'auto'}}
                                    onClick={()=>{
                                        setAddModal()
                                    }}
                                    >
                                    <span style={{fontSize: '22px'}}>&times;</span>
                                </Button>
                            </div>
                        </Col>
                        <Col md={12} className='mb-4'>
                            <div className="d-flex justify-content-between align-items-center">
                                <p className="mb-0" style={{fontWeight: '600', color: '#333', fontSize: '20px'}}>{isAdd ? Translate[lang]?.add : Translate[lang]?.edit} {Translate[lang]?.branch}</p>
                                <Form.Check
                                    type="switch"
                                    id="custom-switch"
                                    checked={formData?.status}
                                    onChange={(e) => setFormData({...formData, status: e.target.checked})}
                                />
                            </div>
                        </Col>
                        <Col md={12}>
                            <AvField
                                label={Translate[lang]?.branch_name}
                                type='text'
                                placeholder={Translate[lang]?.branch_name}
                                bsSize="lg"
                                name='branch_name'
                                validate={{
                                    required: {
                                        value: true,
                                        errorMessage: Translate[lang].field_required
                                    }
                                }}
                                value={formData.branch_name}
                                onChange={(e) => setFormData({...formData, branch_name: e.target.value})}
                            />
                        </Col>
                        <Col md={12} className='mb-3'>
                            <label className="text-label">{Translate[lang]?.branch_manager}</label>
                            <Select
                                value={formData?.branch_manager}
                                name="branch_manager"
                                placeholder={Translate[lang]?.select}
                                options={[]}
                                onChange={(e)=> setFormData({...formData, branch_manager: e})}
                            />
                        </Col>
                        <Col md={12}>
                            <AvField
                                label={Translate[lang]?.branch_id}
                                type='text'
                                placeholder={Translate[lang]?.branch_id}
                                bsSize="lg"
                                name='branch_id'
                                validate={{
                                    required: {
                                        value: true,
                                        errorMessage: Translate[lang].field_required
                                    }
                                }}
                                value={formData.branch_id}
                                onChange={(e) => setFormData({...formData, branch_id: e.target.value})}
                            />
                        </Col>
                        <Col md={12}>
                            <AvField
                                label={Translate[lang]?.branch_no}
                                type='number'
                                placeholder={Translate[lang]?.branch_no}
                                bsSize="lg"
                                name='branch_no'
                                validate={{
                                    required: {
                                        value: true,
                                        errorMessage: Translate[lang].field_required
                                    }
                                }}
                                value={formData.branch_no}
                                onChange={(e) => setFormData({...formData, branch_no: e.target.value})}
                            />
                        </Col>
                        <Col md={12} className='mb-3'>
                            <label className="text-label">{Translate[lang]?.organization}</label>
                            <Select
                                value={formData?.organization}
                                name="organization"
                                placeholder={Translate[lang]?.select}
                                options={[]}
                                onChange={(e)=> setFormData({...formData, organization: e})}
                            />
                        </Col>

                        <Col md={12}>
                            <AvField
                                label={Translate[lang]?.email}
                                type='email'
                                placeholder={Translate[lang]?.email}
                                bsSize="lg"
                                name='email'
                                validate={{
                                    required: {
                                        value: true,
                                        errorMessage: Translate[lang].field_required
                                    }
                                }}
                                value={formData.email}
                                onChange={(e) => setFormData({...formData, email: e.target.value})}
                            />
                        </Col>
                        <Col md={12} className='mb-3'>
                            <label>{Translate[lang]?.phone}</label>
                            <PhoneInput
                            country={"kw"}
                            enableSearch={true}
                            value={formData?.phone}
                            onChange={(e) => setFormData({...formData, phone: e})}
                            />
                        </Col>
                        <Col md={12}>
                            <AvField
                                label={Translate[lang]?.address}
                                type='text'
                                placeholder={Translate[lang]?.address}
                                bsSize="lg"
                                name='address'
                                validate={{
                                    required: {
                                        value: true,
                                        errorMessage: Translate[lang].field_required
                                    }
                                }}
                                value={formData.address}
                                onChange={(e) => setFormData({...formData, address: e.target.value})}
                            />
                        </Col>
                        <Col md={12} className='mb-3'>
                            <label className="text-label">{Translate[lang]?.work_shift}</label>
                            <Select
                                value={formData?.work_shift}
                                name="work_shift"
                                placeholder={Translate[lang]?.select}
                                options={[]}
                                onChange={(e)=> setFormData({...formData, work_shift: e})}
                            />
                        </Col>
                        <Col md={12} className='mb-3'>
                            <label className="text-label">{Translate[lang]?.gates_no}</label>
                            <Select
                                value={formData?.gates_number}
                                name="gates_number"
                                placeholder={Translate[lang]?.select}
                                options={[
                                    {label: '1', value: 1},
                                    {label: '2', value: 2},
                                    {label: '3', value: 3},
                                    {label: '4', value: 4},
                                    {label: '5', value: 5},
                                    {label: '6', value: 6},
                                    {label: '7', value: 7},
                                    {label: '8', value: 8},
                                    {label: '9', value: 9},
                                    {label: '10', value: 10},
                                ]}
                                onChange={(e)=> {
                                    setFormData({
                                        ...formData,
                                        gates: Array(e.value).fill(""),
                                        gates_number: e
                                    })
                                }}
                            />
                        </Col>
                        {formData?.gates?.map((gate, index) => {
                            return <Col md={6} key={index}>
                            <AvField
                                label={Translate[lang]?.gate_no}
                                type='number'
                                placeholder={Translate[lang]?.gate_no}
                                bsSize="lg"
                                name='gate_no'
                                validate={{
                                    required: {
                                        value: true,
                                        errorMessage: Translate[lang].field_required
                                    }
                                }}
                                value={gate}
                                onChange={(e) => {
                                    let update = formData?.gates?.map((res, ind) => {
                                        if(index === ind){
                                            return e.target?.value
                                        } else {
                                            return res
                                        }
                                    })
                                    setFormData({...formData, gates: update})
                                }}
                            />
                        </Col>
                        })}
                    </Row>
            </Modal.Body>
            <Modal.Footer>
            {/* <Button onClick={setAddModal} variant="danger light">
            {Translate[lang]?.close}
            </Button> */}
            <Button 
                    variant="primary" 
                    type='submit'
                    className='w-100'
                    disabled={loading}
                >{isAdd ? Translate[lang]?.add : Translate[lang]?.edit} {Translate[lang]?.branch}</Button>
            </Modal.Footer>
            </AvForm>
        </Modal>)
}

export default AddBranchModal;