import React, { useEffect, useState } from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import OrganizationService from "../../../../services/OrganizationService";
import {useLocation} from 'react-router-dom';
import Select from "react-select";
import {AvField, AvForm} from "availity-reactstrap-validation";
import { useSelector } from "react-redux";
import { Translate } from "../../../Enums/Tranlate";

const AddHall = () => {
   const location = useLocation();
   const [formData, setFormData] = useState({
      branch: "",
      hall_name: "",
      hall_no: "",
      program_type: "",
      coach: "",
      gender: "",
      time_from: "",
      time_to: "",
      number_of_classes: "",
      classes: [],
      capacity_assumption : "",
      capacity: "",
      days: "",
   })
   const [loading, setLoading] = useState(false)
   const [id, setId] = useState(null)
   const [view, setView] = useState(false)
   const navigate = useNavigate()
   const organizationService = new OrganizationService()
   const lang = useSelector(state=> state.auth?.lang)

   useEffect(()=>{
      if(location?.pathname.includes('edit') || location?.pathname.includes('view')){
         let item = location.state
         setId(item?.id)
         // setFormData({
         // })
      }
      if(location?.pathname.includes('view')){
         setView(true)
      }
   },[location])

   const inputHandler = (e) =>{
      setFormData({...formData, [e.target.name]: e.target.value})
   }

   const onSubmit = (e) =>{
      e.preventDefault();
      
      let data = {
      }

      // if(!!id){
      //    setLoading(true)
      //    organizationService.update(id, data).then((response) =>{
      //       if(response?.status === 200){
      //          toast.success(Translate[lang].admin_updated_successfully)
      //          navigate('/organization')
      //       }
      //       setLoading(false)
      //    })
      // } else {
      //    setLoading(true)
      //    organizationService.create(data).then((response) =>{
      //       if(response?.status === 201){
      //          toast.success(`${Translate[lang].added} ${Translate[lang].admin} ${Translate[lang].successfully}`)
      //          navigate('/organization')
      //       }
      //       setLoading(false)
      //    })
      // }
   }

   return (<>
      <div className="d-flex  align-items-center" style={{gap: '8px', margin: '12px 0 32px'}}>
         <svg style={{cursor: 'pointer'}} width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg" onClick={()=> navigate('/halls')}>
            <path d="M9.57 5.92993L3.5 11.9999L9.57 18.0699" stroke="#1E1F24" stroke-width="1.5" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"/>
            <path d="M20.4999 12H3.66992" stroke="#1E1F24" stroke-width="1.5" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"/>
         </svg>

         <label className="mb-0" style={{fontSize: '20px', fontWeight: '600'}}>
            {view ? Translate[lang]?.view : !!id ? Translate[lang]?.edit : Translate[lang]?.add} {Translate[lang]?.hall}
         </label>
         
         <Form.Check
            type="switch"
            id="custom-switch"
            checked={formData?.status}
            onChange={(e) => setFormData({...formData, status: e.target.checked})}
         />
      </div>
      <Row className="add-organization">
         <Col md={10}>
            <AvForm onValidSubmit={onSubmit}>
               <div className="row">
                  <div className="col-lg-6 col-sm-6 ">
                     <label className="text-label">{Translate[lang]?.branch}</label>
                     <Select
                        value={formData?.branch}
                        name="branch"
                        placeholder={Translate[lang]?.select}
                        options={[]}
                        onChange={(e)=> setFormData({...formData, branch: e})}
                     />
                  </div>
                  <div className="col-lg-6 col-sm-6 ">
                     <AvField
                        label ={`${Translate[lang]?.hall_name}`}
                        name ='hall_name'
                        type='text'
                        value={formData?.hall_name}
                        disabled={view}
                        validate={{
                           required: {
                              value:true,
                              errorMessage: Translate[lang].field_required
                           },
                        }}
                        placeholder={`${Translate[lang]?.hall_name}`}
                        onChange={(e)=> inputHandler(e)}
                     />
                  </div>
                  <div className="col-lg-6 col-sm-6 ">
                     <AvField
                        label ={`${Translate[lang]?.hall_no}`}
                        name ='hall_no'
                        type='text'
                        value={formData?.hall_no}
                        disabled={view}
                        validate={{
                           required: {
                              value:true,
                              errorMessage: Translate[lang].field_required
                           },
                        }}
                        placeholder={`${Translate[lang]?.hall_no}`}
                        onChange={(e)=> inputHandler(e)}
                     />
                  </div>
                  <div className="col-lg-6 col-sm-6 ">
                     <label className="text-label">{Translate[lang]?.program_type}</label>
                     <Select
                        value={formData?.program_type}
                        name="program_type"
                        placeholder={Translate[lang]?.select}
                        options={[]}
                        onChange={(e)=> setFormData({...formData, program_type: e})}
                     />
                  </div>
                  <div className="col-lg-6 col-sm-6 ">
                     <AvField
                        label ={`${Translate[lang]?.coach}`}
                        name ='coach'
                        type='text'
                        value={formData?.coach}
                        disabled={view}
                        validate={{
                           required: {
                              value:true,
                              errorMessage: Translate[lang].field_required
                           },
                        }}
                        placeholder={`${Translate[lang]?.coach}`}
                        onChange={(e)=> inputHandler(e)}
                     />
                  </div>
                  <div className="col-lg-6 col-sm-6 ">
                     <label className="text-label">{Translate[lang]?.gender}</label>
                     <Select
                        value={formData?.gender}
                        name="gender"
                        placeholder={Translate[lang]?.select}
                        options={[]}
                        onChange={(e)=> setFormData({...formData, gender: e})}
                     />
                  </div>
                  <div className="col-lg-12 col-sm-12 mb-3 ">
                     <Row className='table-style py-2 w-100 m-auto '>
                        <div className="col-lg-4 col-sm-4 ">
                           <AvField
                              label ={`${Translate[lang]?.time_from}`}
                              name ='time_from'
                              type='time'
                              value={formData?.time_from}
                              disabled={view}
                              validate={{
                                 required: {
                                    value:true,
                                    errorMessage: Translate[lang].field_required
                                 },
                              }}
                              placeholder={`${Translate[lang]?.time_from}`}
                              onChange={(e)=> inputHandler(e)}
                           />
                        </div>
                        <div className="col-lg-4 col-sm-4 ">
                           <AvField
                              label ={`${Translate[lang]?.time_to}`}
                              name ='time_to'
                              type='time'
                              value={formData?.time_to}
                              disabled={view}
                              validate={{
                                 required: {
                                    value:true,
                                    errorMessage: Translate[lang].field_required
                                 },
                              }}
                              placeholder={`${Translate[lang]?.time_to}`}
                              onChange={(e)=> inputHandler(e)}
                           />
                        </div>
                        <div className="col-lg-4 col-sm-4 ">
                           <AvField
                              label ={`${Translate[lang]?.number_of_classes}`}
                              name ='number_of_classes'
                              type='number'
                              value={formData?.number_of_classes}
                              disabled={view}
                              min={0}
                              validate={{
                                 required: {
                                    value:true,
                                    errorMessage: Translate[lang].field_required
                                 },
                              }}
                              placeholder={`${Translate[lang]?.number_of_classes}`}
                              onChange={(e)=> {
                                 let newNumber = parseInt(e.target.value);
                                 if(!newNumber){
                                    return
                                 }
                                 setFormData((prevArray) => {
                                    if (newNumber > prevArray?.classes?.length) {
                                      // Add new objects if the number is greater
                                      const newObjects = Array.from(
                                        { length: newNumber - prevArray?.classes?.length },
                                        (_) => ({
                                          from: "", to: "", interval: ""
                                        })
                                      );
                                      return {...prevArray, classes: [...prevArray?.classes, ...newObjects]};
                                    } else {
                                      return {...prevArray, classes: prevArray?.classes?.slice(0, newNumber)}
                                    }
                                  })
                              }}
                           />
                        </div>
                        {formData?.classes?.map((classItem, index) => {
                           return <React.Fragment key={index}>
                              <div className="col-lg-4 col-sm-4 ">
                                 <AvField
                                    label ={`${Translate[lang]?.class} ${index+1} ${Translate[lang]?.from}`}
                                    name ={`from${index}`}
                                    type='time'
                                    value={classItem.from}
                                    disabled={view}
                                    validate={{
                                       required: {
                                          value:true,
                                          errorMessage: Translate[lang].field_required
                                       },
                                    }}
                                    placeholder={`${Translate[lang]?.from}`}
                                    onChange={(e)=> {
                                       setFormData((prevData) => ({
                                          ...prevData,
                                          classes: prevData.classes?.map((item, ind) =>
                                          ind === index
                                             ? { ...item, from: e.target.value }
                                             : item 
                                          ),
                                       }));
                                    }}
                                 />
                              </div>
                              <div className="col-lg-4 col-sm-4 ">
                                 <AvField
                                    label ={`${Translate[lang]?.class} ${index+1} ${Translate[lang]?.to}`}
                                    name ={`to${index}`}
                                    type='time'
                                    value={classItem.to}
                                    disabled={view}
                                    validate={{
                                       required: {
                                          value:true,
                                          errorMessage: Translate[lang].field_required
                                       },
                                    }}
                                    placeholder={`${Translate[lang]?.to}`}
                                    onChange={(e)=> {
                                       setFormData((prevData) => ({
                                          ...prevData,
                                          classes: prevData.classes?.map((item, ind) =>
                                          ind === index
                                             ? { ...item, to: e.target.value }
                                             : item 
                                          ),
                                       }));
                                    }}
                                 />
                              </div>
                              <div className="col-lg-4 col-sm-4 ">
                                 <AvField
                                    label ={`${Translate[lang]?.interval_between_classes}`}
                                    name ={`interval${index}`}
                                    type='number'
                                    value={classItem.interval}
                                    disabled={view}
                                    validate={{
                                       required: {
                                          value:true,
                                          errorMessage: Translate[lang].field_required
                                       },
                                    }}
                                    placeholder={`${Translate[lang]?.number}`}
                                    onChange={(e)=> {
                                       setFormData((prevData) => ({
                                          ...prevData,
                                          classes: prevData.classes?.map((item, ind) =>
                                          ind === index
                                             ? { ...item, interval: e.target.value }
                                             : item 
                                          ),
                                       }));
                                    }}
                                 />
                              </div>
                           </React.Fragment>
                        })}
                     </Row>
                  </div>
                  <div className="col-lg-6 col-sm-6 ">
                     <AvField
                        label ={`${Translate[lang]?.capacity}`}
                        name ='capacity'
                        type='text'
                        value={formData?.capacity}
                        disabled={view}
                        validate={{
                           required: {
                              value:true,
                              errorMessage: Translate[lang].field_required
                           },
                        }}
                        placeholder={`${Translate[lang]?.capacity}`}
                        onChange={(e)=> inputHandler(e)}
                     />
                  </div>
                  <div className="col-lg-6 col-sm-6 ">
                     <AvField
                        label ={`${Translate[lang]?.capacity_assumption}`}
                        name ='capacity_assumption'
                        type='text'
                        value={formData?.capacity_assumption}
                        disabled={view}
                        validate={{
                           required: {
                              value:true,
                              errorMessage: Translate[lang].field_required
                           },
                        }}
                        placeholder={`${Translate[lang]?.capacity_assumption}`}
                        onChange={(e)=> inputHandler(e)}
                     />
                  </div>
                  <div className="col-lg-6 col-sm-6">
                  <label className="text-label">{Translate[lang]?.days}</label>
                     <Select
                        value={formData?.days}
                        name="days"
                        placeholder={Translate[lang]?.select}
                        options={[]}
                        onChange={(e)=> setFormData({...formData, days: e})}
                     />
                  </div>
                  <div className="col-lg-6 col-sm-6"></div>
                  <div className="col-lg-6 col-sm-6 mt-4 mb-5">
                     {view && <Button variant="secondary" type="button" className='w-100 fs-14' onClick={()=> navigate('/halls')}>{Translate[lang]?.cancel}</Button>}
                     {!view && <Button variant="primary" type="submit" className='w-100 fs-14' disabled={loading}>{Translate[lang]?.save}</Button>}
                  </div>
               </div>
            </AvForm>
         </Col>
      </Row>
   </>
   );
};

export default AddHall;
