import { useState } from "react";
import { Badge, Dropdown } from "react-bootstrap";
import { useSelector } from "react-redux";
import DepartmentService from "../../../../services/DepartmentService";
import DeleteModal from "../../../common/DeleteModal";
import { Translate } from "../../../Enums/Tranlate";
import editIcon from "../../../../images/icons/edit.svg";
import trashIcon from "../../../../images/icons/delete.svg";

const CardItem = ({item, setItem, index, setAddModal,setViewModal, setShouldUpdate}) =>{
    const [deleteModal, setDeleteModal] = useState(false)
    const Auth = useSelector(state=> state.auth?.auth)
    const lang = useSelector(state=> state.auth?.lang)
    const isExist = (data)=> Auth?.admin?.admin_roles?.includes(data)
    const departmentService = new DepartmentService()

    return(
        <tr key={index} className='text-center'>
            <td>
                {item?.name}
            </td>
            <td>{'Mohamed'}</td>
            <td>Kuwait</td>
            <td>2</td>
            <td>{item.number_of_people}</td>
            <td>
                {/* <i className="la la-eye cursor-pointer" 
                    onClick={()=> {
                        setItem(item)
                        setViewModal(true)
                    }} 
                    style={{
                        fontSize: '27px', 
                        color: '#666'
                    }}
                ></i> */}
                <Badge
                    className="text-capitalize"
                    variant={`${
                        item.status === "delivered"
                        ? "success"
                        : item.status === "canceled"
                        ? "danger"
                        : item.status === "ordered"
                        ? "primary"
                        : item.status === "pending"
                        ? "warning"
                        : item.status === "success"
                        ? "info"
                        : "success"
                    } light`}
                >
                    {item?.status || 'Active'}
                </Badge>
            </td>
            <td>
                <div className="d-flex" style={{gap: '20px'}}>
                    <img src={editIcon} alt='edit' width={24} />
                    <img src={trashIcon} alt='trash' width={24} />
                </div>
                {/* {(isExist('edit_departments') || isExist('delete_departments')) && <Dropdown>
                    <Dropdown.Toggle
                        className="light sharp i-false"
                    >
                        <i className="la la-ellipsis-v" style={{fontSize: '27px'}}></i>
                    </Dropdown.Toggle>
                    <Dropdown.Menu>
                        {isExist('edit_departments') && <Dropdown.Item onClick={()=> {
                            setItem(item)
                            setAddModal(true)
                        }}> 
                            <i className='la la-edit'></i> {Translate[lang]?.edit}
                        </Dropdown.Item>}
                        {isExist('delete_departments') && <Dropdown.Item onClick={()=> setDeleteModal(true)}>
                        <i className='la la-trash'></i> {Translate[lang]?.delete}
                        </Dropdown.Item>}
                    </Dropdown.Menu>
                </Dropdown>} */}
            </td>
            {deleteModal && <DeleteModal
                      open={deleteModal}
                      titleMsg={item?.name}
                      deletedItem={item}
                      modelService={departmentService}
                      onCloseModal={setDeleteModal}
                      setShouldUpdate={setShouldUpdate}
                    />}
            </tr>
    )
}
export default CardItem;